
import React, { Component } from 'react';
import ReactApexChart from 'react-apexcharts';
import { BaseUrl } from "../../helpers/Baseurl"


class ByCompany extends Component {
    constructor(props) {
        super(props);

        this.state = {
            users : [],
            series: [{
                name: 'Bidding',
                data: []
            }, {
                name: 'Delivered',
                data: []
            }, {
                name: 'Sold',
                data: []
            },
            {
                name: 'Lost',
                data: []
            },
            {
                name: 'Completed',
                data: []
            }],
            
        };
    }
    async componentDidMount () {
        // this.loadData()
        const requestOptions = {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
          };
        await fetch(`${BaseUrl}/api/jobs/sold_by_company/`,requestOptions)
              .then(res => res.json())
              .then(
                  (data) => {
                      console.log(data, '-data')
                      let series = [{
                          name: 'Bidding',
                          data: data.bidding
                      }, {
                          name: 'Delivered',
                          data:data.delivered
                      }, {
                          name: 'Sold',
                          data: data.sold
                      },
                      {
                          name: 'Lost',
                          data: data.lost
                      },
                      {
                          name: 'Completed',
                          data:data.completed
                      }]
                      
                      this.setState({ 
                          series:series,
                          users: data.company
                      })           
                     
                  },
                  (error) => {
                      console.log(error, 'error--------------') 
                  }
              )  
    }

    render() {
        let options = {
            chart: {
                toolbar: {
                    show: false,
                }
            },
            plotOptions: {
                bar: {
                    horizontal: false,
                    columnWidth: '45%',
                    endingShape: 'rounded'	
                },
            },
            dataLabels: {
                enabled: false
            },
            stroke: {
                show: true,
                width: 2,
                colors: ['transparent']
            },
            
            colors: ['#1cbb8c', '#eeb902','#58a5ff','#5664d2', '#ff3d60'],
            xaxis: {
                categories:this.state.users,
            },
            yaxis: {
                title: {
                    text: ''
                }
            },
            grid: {
                borderColor: '#f1f1f1',
            },
            fill: {
                opacity: 1
        
            },
            tooltip: {
                y: {
                    formatter: function (val) {
                        return  val
                    }
                }
            }
        }
        return (
            <React.Fragment>
                <ReactApexChart options={options} series={this.state.series} type="bar" height={350} />
            </React.Fragment>
        );
    }
}

export default ByCompany;
